import React, { useEffect, useState } from 'react';
import './header.scss';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';

function Header({ scrollHome, scrollAbout, scrollOffer, scrollFooter, scrollCalculator }) {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY;
      setIsScrolled(scrollTop > 0); // Check if scrolled beyond the top
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);


  return (
    <header id='header'>
    <div className='header-contact'>
      <div className='contact-container'>
        <PhoneAndroidIcon></PhoneAndroidIcon>+48 123456789
        <div style={{width: '15px'}}></div>
        <MailOutlineIcon></MailOutlineIcon> leszekindyk@gmail.com
      </div>
      <div className='contact-container'>
        <a style={{textDecoration: 'none', listStyle: 'none', color: 'white', display: 'flex', alignItems: 'center', transition: 'all 0.5s'}} href='https://www.facebook.com/julia.czajka.946' target='_blank'><FacebookIcon className='scaleEffectFB'></FacebookIcon></a>
        <div style={{width: '5px'}}></div>
        <a style={{textDecoration: 'none', listStyle: 'none', color: 'white', display: 'flex', alignItems: 'center', transition: 'all 0.5s'}} href='https://www.instagram.com/wojtekgola/' target='_blank'><InstagramIcon  className='scaleEffectINS'></InstagramIcon></a>
      </div>
    </div>
    <div  className='header-details'>
      <div><a onClick={scrollHome}> <div className='logo'></div></a></div>
      <ul className='menu'>
        <li><a onClick={scrollAbout}>Leasingi</a></li>
        <li><a onClick={scrollOffer}>Kredyty</a></li>
        <li><a onClick={scrollOffer}>Korzysci</a></li>
        <li><a onClick={scrollCalculator}>Kalkulator</a></li>
        <li><a onClick={scrollAbout}>O nas</a></li>
        <li><a onClick={scrollFooter}>Kontakt</a></li>
      </ul>
    </div>
    </header>
  );
}

export default Header;
