import React from 'react';
import './creditsOffer.scss';
import DescriptionIcon from '@mui/icons-material/Description';
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import PublishedWithChangesIcon from '@mui/icons-material/PublishedWithChanges';

const CreditsOffer = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="creditsOffer">
      <div className='creditsOfferContainer'>
        <div className='productDescription'>
          <span className='productDesc'>Nasze uslugi</span>
          <h1>RODZAJE KREDYTOW</h1>
        </div>
        <div className="creditCardsContainer">
          <div className="cardsOffer">
            <DescriptionIcon className='iconTopic'></DescriptionIcon>
            <h3>Kredyt hipoteczny</h3>
            <p>Spenij marzenia o wasnym M z
              nasz pomoc! Porwnamy
              oferty najwikszych bankw na
              polskim rynku i znajdziemy t
              najkorzystniejsz dla Ciebie.
              Zapewnimy kompleksow
              obsug na kadym etapie - od
              porwnania ofert, przez dobr
              odpowiedniego kredytu, a po
              pomoc w formalnociach.
            </p>
          </div>
          <div className="cardsOffer">
            <MonetizationOnIcon className='iconTopic'></MonetizationOnIcon>
            <h3>Kredyt gotowkowy</h3>
            <p>Zrealizuj swoje plany ju dzi!
              Zapewniamy szybki i atwy
              dostp do rodkw na dowolny
              cel, np. remont, wymarzon
              wycieczk czy nieprzewidziane

              wydatki.
            </p>
          </div>
          <div className="cardsOffer">
            <PublishedWithChangesIcon className='iconTopic'></PublishedWithChangesIcon>
            <h3>Refinansowanie
              kredytu hipotecznego</h3>
            <p>Zmieniy si stopy procentowe na
              korzystniejsze, a Ty wci pacisz
              tak samo wysok rat? Sprawd

              oferty refinansowania!
              Pomoemy Ci przenie Twj
              kredyt hipoteczny do innego
              banku na lepszych warunkach,
              tym samym Ty obniysz swoj
              rat i zaoszczdzisz pienidze.
            </p>
          </div>
        </div>
      </div>
    </section>
  );
})

export default CreditsOffer;
