import React from 'react';
import './leasings.scss';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';

const Leasings = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="leasings">
      <div className='leftContainer'>
        <div className='logoContainer'>
          <DirectionsCarIcon style={{ fontSize: '10rem' }}></DirectionsCarIcon>
          <p className="descriptionLogo">Leasing</p>
        </div>
      </div>
      <div className='rightContainer'>
        <div className='productDescription'>
          <span className='productDesc'>O PRODUKCIE</span>
          <h1>CZYM JEST LEASING?</h1>
          <p>Leasing to forma finansowania, ktra pozwala na
            uytkowanie nowego lub uywanego przedmiotu (np.
            samochodu, maszyny, nieruchomoci) przez okrelony
            czas w zamian za opaty zwane ratami leasingowymi.</p>
          <p>W praktyce oznacza to, e leasingobiorca moe
            korzysta z danego dobra bez koniecznoci jego
            natychmiastowego zakupu.
            Leasing jest popularny wrd przedsibiorstw, poniewa
            pozwala na optymalizacj kosztw oraz zarzdzanie
            pynnoci finansow. Dodatkowo, umowy leasingowe
            czsto s bardziej elastyczne ni tradycyjne kredyty
            bankowe.</p>

          <p>Z nami zyskujesz dostp do blisko 40 starannie
            wyselekcjonowanych firm leasingowych i bankw,
            czoowych graczy na rynku. Dziki temu moesz mie
            pewno, e znajdziemy idealn ofert, dopasowan
            precyzyjnie do Twoich potrzeb i oczekiwa.</p>
        </div>
      </div>
    </section>
  );
})

export default Leasings;
