import React from 'react';
import './credits.scss';
import ApartmentIcon from '@mui/icons-material/Apartment';

const Credits = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="credits">
      <div className='leftContainer'>
        <div className='logoContainer'>
            <ApartmentIcon style={{fontSize: '10rem'}}></ApartmentIcon>
            <p className="descriptionLogo">Kredyty</p>
        </div>
      </div>
      <div className='rightContainer'>
        <div className='productDescription'>
            <span className='productDesc'>O PRODUKCIE</span>
            <h1>SZUKASZ KREDYTU?</h1>
            <p>Szukasz finansowania i nie wiesz do ktrego banku si
              uda? Nie tra czasu i skorzystaj z naszej pomocy
              zupenie za darmo. Niezalenie od rodzaju kredytu i jego
              przeznaczenia znajdziemy finansowanie ktre speni
              Twoje oczekiwania.
              Wsppracujemy z blisko 20 wiodcymi bankami na
              rynku, dziki temu w jednym miejscu porwnasz oferty
              kadego z nich, a nastpnie zoymy wnioski kredytowe
              oszczdzajc tym samym Twj czas i pienidze!</p>
            </div>
      </div>
    </section>
  );
})

export default Credits;
