import React from 'react';
import './about.scss';
import PhoneIcon from '@mui/icons-material/Phone';

const About = React.forwardRef((props, ref) => {
  return (
    <section  ref={ref} id="about" className="about">
      <div className="logo-container">
        <div className='logo'></div>
      </div>
      <div className="description">
        <p>
        Szukasz idealnego produktu finansowego, ale gubisz si w gszczu ofert i skomplikowanych zapisw? Skorzystaj z bezpatnej pomocy dowiadczonego porednika!
        </p>
        <p>
        Nasi eksperci pomog Ci wybra produkt finansowy skrojony na miar Twoich potrzeb i moliwoci, oszczdzajc Twj cenny czas i negocjujc dla Ciebie najkorzystniejsze warunki. Nasze usugi s cakowicie darmowe dla klienta - wynagrodzenie otrzymujemy od bankw i instytucji finansowych.
        </p>
        <p>Skontaktuj si z nami ju dzi i przekonaj si, jak moemy Ci pomc!
        </p>
      </div>
    </section>
  );
})

export default About;
