import React from 'react';
import './ourProducts.scss';
import ImportantDevicesIcon from '@mui/icons-material/ImportantDevices';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const OurProducts = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="ourProducts">
      <div className='leftContainer'>
        <div className='logoContainer'>
          <ImportantDevicesIcon style={{ fontSize: '10rem' }}></ImportantDevicesIcon>
          <p className="descriptionLogo">Produkty Firmowe</p>
        </div>
      </div>
      <div className='rightContainer'>
        <div className='productDescription'>
          <span className='productDesc'>O PRODUKCIE</span>
          <h1>NASZE PRODUKTY</h1>
          <p style={{fontSize: '0.9rem'}}>W prowadzeniu dziaalnoci gospodarczej kluczow rol
            odgrywa odpowiednie zarzdzanie finansami. Dostpne
            na rynku produkty finansowe dla firm, takie jak kredyty
            obrotowe, kredyty inwestycyjne, czy faktoring mog
            znaczco uatwi ten proces i pomc w rozwoju
            przedsibiorstwa!</p>
          <div className='rowParagraph'>
            <CheckCircleIcon />
            <p> Kredyt obrotowy - to krtkoterminowa poyczka
              udzielana firmom w celu finansowania biecej
              dziaalnoci.
              Suy do pokrycia biecych kosztw, takich jak
              wynagrodzenia pracownikw, zakupy towarw i usug,
              czynsz i inne zobowizania</p>
          </div>
          <div className='rowParagraph'>
            <CheckCircleIcon />
            <p> Kredyt inwestycyjny - to dugoterminowa poyczka
              udzielana firmom w celu sfinansowania przedsiwzi
              majcych na celu rozwj lub modernizacj dziaalnoci.
              Moe by przeznaczony na przykad na zakup
              nieruchomoci, maszyn, urzdze lub oprogramowania, a
              take na badania i rozwj.</p>
          </div>
          <div className='rowParagraph'>
            <CheckCircleIcon />
            <p> Faktoring - dziki faktoringowi przedsibiorca
              odmraa swoje faktury - zamiast czeka 30 albo 60 dni
              na zapat od kontrahenta, moe dosta pienidze nawet
              w cigu 24 godzin od przekazania faktur do firmy
              faktoringowej. Zamie faktury na gotwk. Pozbd si
              ryzyka i zapewnij pynno finansow swojej firmie.</p>
          </div>
        </div>
      </div>
    </section>
  );
})

export default OurProducts;
