import React from 'react';
import './leasingOffer.scss';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const LeasingOffer = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="leasingOffer">
      <div className='creditsOfferContainer'>
        <div className='productDescription'>
            <span className='productDesc'>Nasze uslugi</span>
            <h1>METODY FINANSOWANIA</h1>
        </div>
        <div className="creditCardsContainer">
            <div className='topRow'>
                    <div className="cardsOffer">
                    <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Leasing
                        operacyjny</h3>

                    </div>
                    <div className="cardsOffer">
                    <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Leasing
                        finansowy</h3>
                    </div>
                    <div className="cardsOffer">
                        <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Leasing
                        konsumencki</h3>

                    </div>
                    <div className="cardsOffer">
                    <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Leasing
                        bez BIK I KRD</h3>

                    </div>
            </div>
            <div className='bottomRow'>
                    <div className="cardsOffer">
                        <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Leasing
                        zwrotny</h3>

                    </div>
                    <div className="cardsOffer">
                    <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Poyczk
                        leasingowa</h3>

                    </div>
                    <div className="cardsOffer">
                        <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Wynajem

dlugoterminowy</h3>

                    </div>
                    <div className="cardsOffer">
                    <CheckCircleIcon className='iconTopic'></CheckCircleIcon>
                        <h3>Kredyt

samochodowy</h3>

                    </div>
            </div>
        </div>
      </div>
    </section>
  );
})

export default LeasingOffer;
