import React, {useRef, useState} from 'react';
import { Helmet } from 'react-helmet';
import Header from './components/header';
import Footer from './components/footer';
import Home from './components/home';
import About from './components/about';
import Calculator from './components/calculator';
import BanksCarousel from './components/banksCarousel';
import WhoWeAre from './components/whoweare';
import Credits from './components/credits';
import CreditsOffer from './components/creditsOffer';
import Leasings from './components/leasings';
import LeasingOffer from './components/leasingOffer';
import LeasingItems from './components/leasingItems';
import OurProducts from './components/ourProducts';
import WorkWithUS from './components/workWithUs';
import OurTeam from './components/ourTeam';



function App() {

  const [scrollHeight, setScrollHeight] = useState(0);
  const [section, setSection] = useState(0)
  
  
  const homeRef = useRef(null);
  const aboutRef = useRef(null);
  const ourOfferRef = useRef(null);
  const banksCarouselRef = useRef(null);
  const calculatorRef = useRef(null);
  const footerRef = useRef(null);

  const scrollToCenter = (ref) => {
    if (ref && ref.current) {
      let scrollPosition = ref.current.offsetTop + ref.current.offsetHeight / 2 - window.innerHeight / 2;
      
   
      window.scrollTo({
        top: scrollPosition,
        behavior: 'smooth'
      });
    }
  };

  return (
    <div style={{width: '100vw'}}>
      <Helmet>
        <title>Leasing i Kredyty - Polska</title>
        <meta name="description" content="Oferujemy najlepsze rozwizania leasingowe i kredytowe w Polsce." />
      </Helmet>
      <Header scrollHome={()=> scrollToCenter(homeRef)} scrollAbout={()=> scrollToCenter(aboutRef)} scrollOffer={()=> scrollToCenter(ourOfferRef)} scrollFooter={()=> scrollToCenter(footerRef)} scrollCalculator={()=> scrollToCenter(calculatorRef)}/>
      <Home ref={homeRef} scrollOffers={()=> scrollToCenter(ourOfferRef)}/>
      <WhoWeAre></WhoWeAre>
      <Calculator ref={calculatorRef}/>
      {/* <About ref={aboutRef}/> */}
      <Credits ref={ourOfferRef}/>
      <CreditsOffer></CreditsOffer>
      <BanksCarousel ref={banksCarouselRef}/>
      <Leasings/>
      <LeasingOffer></LeasingOffer>
      <LeasingItems></LeasingItems>
      <OurProducts></OurProducts>
      <WorkWithUS></WorkWithUS>
      <OurTeam></OurTeam>
      <Footer ref={footerRef}/> 
    </div>
  );
}

export default App;
