import React, { useState } from 'react';
import './calculator.scss';


const Calculator = React.forwardRef((props, ref) => {


  return (
    <section  ref={ref} id="calculator" className="calculator">
      <div className="description">
        <h2>Sprawdzimy twoj zdolno kredytow!</h2>
        <p>
        Doradca finansowy syszka posiada uprawnienia systemowe do korzystania bezporednio z kalkulatorw bakowych! Dziki temu doradca dokadnie sprawdzi Twoj zdolno kredytow, jeszcze zanim zawnioskujesz o kredyt! Da Ci to pewno, e kwota kredytu nie ulegnie zmniejszeniu w trakcie procesu kredytowego.
        </p>
      </div>
      <div className="descriptionHelp">
        <p>Chcesz wiedziec wicej lub potrzebujesz pomocy?</p>
        <p className="contactBTN">Skontaktuj si z nami</p>
      </div>
  </section>
  );
})

export default Calculator;
